class Home extends React.Component {  
      
    constructor(){  
        super();  
        this.state = {  
        	id: "home",
            message: "my friend (from state)!"
        };  
        this.updateMessage = this.updateMessage.bind(this);  
    }
    updateMessage() {  
        this.setState({  
            message: "my friend (from changed state)!"  
        });  
    }
    render() {  
         return (  
           <Panel id={this.state.id}>
             Hello {this.state.message}
           </Panel> 
        )  
    }  
}

 export default Home;